.ProductData{
    width: 100%;
}
.ProductTitle{
    font-family: 'Source Sans Pro', sans-serif;
    font-size: 48px;
    font-weight: 600;
    margin-top: 0;
    margin-bottom: 16px;
}
.ProductDescription{
    font-size: 20px;
    color: rgba(0, 0, 0, 0.8);
}
.SectionHeading{
    font-size: 28px;
    margin-bottom: 8px;
}
.ProductImage{
    width: 100%;
    max-width: 80px;
    border-radius: 8px;
    border: 2px solid transparent;
    margin-right: 12px;
    cursor: pointer;
}
.ProductImage:hover{
    border: 2px solid teal;
}
.FeatureItem{
    background-color: gainsboro;
    min-width: 80px;
    border: 0;
    margin-right: 12px;
    text-align: center;
    border-radius: 8px;
    padding: 12px 24px;
    font-weight: 600;
    cursor: pointer;
    box-sizing: content-box;
}
.FeatureItem:hover{
    color: white;
    background-color: gray;
}
.PrimaryButton{
    color:white;
    background-color:teal;
    border-radius: 8px;
    padding: 12px 24px;
    border: 0;
    margin-top: 36px;
    font-size: 20px;
}
.TimeSection{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: white;
    font-size: 32px;
    font-weight: 600;
}
.HeartBeat{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: white;
    font-size: 32px;
    font-weight: 600;
}
.HeartBeat>i{
    color: red;
    animation: beat 1s initial;
}
.HeartBeat>p{
    margin-top: 16px;
}
@keyframes beat{
    0%{
        transform: scale(1,1);
    }
    50%{
        transform:scale(1.2, 1.2);
    }
    100%{
        transform:scale(1,1);
    }
}